.content_container {
  overflow: auto;
}

.wordle {
  margin: 0 auto;
}

.title_bar {
  margin: 0 auto;
  max-width: 500px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #d3d6da;
}

.title {
  font-weight: 700;
  font-size: 36px;
  letter-spacing: 0.2rem;
  flex-grow: 1;
  text-align: center;
}

.history_row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Roboto";
}

.toolbar {
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.toolbar_left {
  margin-left: 8px;
}

.num_guesses_setting {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  margin-top: 8px;
}

.num_characters_setting {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  justify-content: space-between;
}

.spacer {
  flex-grow: 1;
}

.toolbar_right {
  margin-right: 8px;
}

.guesses {
  max-width: 500px;
  margin: 0 auto;
}

.guess_row,
.keyboard_row {
  display: flex;
  justify-content: center;
}

.guess_row.invalid {
  animation-name: Shake;
  animation-duration: 600ms;
}

.guess_row.won {
  animation-name: Bounce;
  animation-duration: 1000ms;
}

@keyframes Bounce {
  0%,
  20% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  50% {
    transform: translateY(5px);
  }
  60% {
    transform: translateY(-15px);
  }
  80% {
    transform: translateY(2px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes Shake {
  10%,
  90% {
    transform: translateX(-1px);
  }
  20%,
  80% {
    transform: translateX(2px);
  }
  30%,
  50%,
  70% {
    transform: translateX(-4px);
  }
  40%,
  60% {
    transform: translateX(4px);
  }
}

.guess_character_div_4 {
  width: min((100vw - 32px) / 4 - 8px, 62px);
  height: min((100vw - 32px) / 4 - 8px, 62px);
  margin: 2px;
}
.guess_character_div_5 {
  width: min((100vw - 32px) / 5 - 8px, 62px);
  height: min((100vw - 32px) / 5 - 8px, 62px);
  margin: 2px;
}
.guess_character_div_6 {
  width: min((100vw - 32px) / 6 - 8px, 62px);
  height: min((100vw - 32px) / 6 - 8px, 62px);
  margin: 2px;
}
.guess_character_div_7 {
  width: min((100vw - 32px) / 7 - 8px, 62px);
  height: min((100vw - 32px) / 7 - 8px, 62px);
  margin: 2px;
}

.guess_character_empty {
  border: 2px solid #d3d6da;
}

.guess_character {
  font-size: 32px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.guess_character_unknown,
.guess_character_not_used {
  border: 2px solid #787c7e;
}

.guess_character_wrong_position {
  border: 2px solid #c9b458;
}

.guess_character_correct {
  border: 2px solid #6aaa64;
}

.keyboard {
  width: min(100vw, 500px);
  margin: 0 auto;
  position: fixed;
  left: calc(50% - min(100vw, 500px) / 2);
  bottom: 0;
  background: white;
  height: 200px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.keyboard_character {
  width: min((100vw - 16px) / 10 - 4px, 40px);
  height: 58px;
  border-radius: 4px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Clear Sans", "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  margin: 2px;
  cursor: pointer;
}

.keyboard_character_wide {
  width: calc(min((100vw - 16px) / 10 - 4px, 40px) * 1.5 + 2px);
}

.status_unknown {
  background: #d3d6da;
}

.status_not_used {
  background: #787c7e;
  color: white;
}

.status_wrong_position {
  background: #c9b458;
  color: white;
}

.status_correct {
  background: #6aaa64;
  color: white;
}

.notification_message {
  display: flex;
  justify-content: center;
  font-size: 16px;
  font-weight: bold;
  background: black;
  color: white;
  border-radius: 8px;
  align-items: center;
  padding: 16px;
  margin-top: 100px;
}

@media only screen and (max-width: 760px) {
  .title {
    font-size: 22px;
    letter-spacing: 0.1rem;
  }

  .history_row {
    font-size: 14px;
  }

  .history_date {
    display: none;
  }
}
